import Vue from 'vue';
import Main from './main.vue'

const UploadConstructor = Vue.extend(Main)

let instance;

/**
 * 
 * @options list          Array              
 * @options reqType       String               "general_file"
 * @options folderId      String         		 ID reqType pan_comp_filepan_my_filepan_share_file
 * @options projectId     String             ID reqType pan_project_file
 * @options resId         String             ID reqType replace_file
 * @options callback			Function           
 */

const Upload = options => {
	options = options || {};
	if (typeof options === 'string') {
	  options = {
	    upload: options
	  }
	}

	if(!!options.list){
		options.list = JSON.parse(JSON.stringify(options.list));
	}

	instance = new UploadConstructor({
    data: options
  });
  document.getElementById('app').appendChild(instance.$mount().$el);
  	
}

export default Upload


