import {timesToDate, changeCronToString,changeNumToChinese} from '@/config/utils'


export const filter = (Vue) => {
  Vue.filter('timesToDate', (times, pattern) => {//
    if (!pattern) {
      pattern = 'yyyy-MM-dd HH:mm';
    }
    if (times) {
      return timesToDate(times, pattern);
    } else {
      return '';
    }
  })
  Vue.filter('filterOrgType', (str) => {//
    var result = "";
    switch(str){
      case 'SYSTEM': result = ''; break;
      case 'PLAT': result = ''; break;
      case 'UNION': result = ''; break;
    }
    return result;
  })

  Vue.filter('reflectStatus', (str) => {//
    var result = "";
    switch(str){
      case 'WAITING_APPROVAL': result = ''; break;
      case 'WAITING_PAY': result = ''; break;
      case 'CASH_DONE': result = ''; break;
      case 'APPROVAL_REJECT': result = ''; break;
      case 'PAY_REJECT': result = ''; break;
    }
    return result;
  })

  Vue.filter('salaryStatus', (str) => {//
    var result = "";
    switch(str){
      case 0: result = ''; break;
      case 1: result = ''; break;
      case 2: result = ''; break;
    }
    return result;
  })

  Vue.filter('filterAutoPay', (str) => {//
    var result = "";
    switch(str){
      case 0: result = ''; break;
      case 1: result = ''; break;
    }
    return result;
  })

  Vue.filter('actorStatus', (str) => {//
    var result = "";
    switch(str){
      case 0: result = ''; break;
      case 1: result = ''; break;
      case -1: result = ''; break;
    }
    return result;
  })

  Vue.filter('distributeStatus', (str) => {//
    var result = "";
    switch(str){
      case 0: result = ''; break;
      case 1: result = ''; break;
    }
    return result;
  })
  Vue.filter('filterMoney', (money) => {//
    return money.toFixed(2);
  })
  



}



