<template>
	<div class="box">
		<h1><a href="http://element-cn.eleme.io/#/zh-CN/component/installation" target="_blank">Element</a></h1>
		<div class="group">
			<el-button></el-button>
			<el-button type="primary"></el-button>
			<el-button type="success"></el-button>
			<el-button type="info"></el-button>
			<el-button type="warning"></el-button>
			<el-button type="danger"></el-button>
		</div>

		<div class="group">
			<el-button plain></el-button>
			<el-button type="primary" plain></el-button>
			<el-button type="success" plain></el-button>
			<el-button type="info" plain></el-button>
			<el-button type="warning" plain></el-button>
			<el-button type="danger" plain></el-button>
		</div class="group">

		<div class="group">
			<el-button round></el-button>
			<el-button type="primary" round></el-button>
			<el-button type="success" round></el-button>
			<el-button type="info" round></el-button>
			<el-button type="warning" round></el-button>
			<el-button type="danger" round></el-button>
		</div class="group">

		<div class="group">
			<el-button disabled></el-button>
			<el-button type="primary" disabled></el-button>
			<el-button type="success" disabled></el-button>
			<el-button type="info" disabled></el-button>
			<el-button type="warning" disabled></el-button>
			<el-button type="danger" disabled></el-button>
		</div>

		<div class="group">
			<el-button plain disabled></el-button>
			<el-button type="primary" plain disabled></el-button>
			<el-button type="success" plain disabled></el-button>
			<el-button type="info" plain disabled></el-button>
			<el-button type="warning" plain disabled></el-button>
			<el-button type="danger" plain disabled></el-button>
		</div>
		<div class="group">
			<el-button type="primary" :loading="true"></el-button>
		</div>
		<div class="group">
			<el-button></el-button>
			<el-button size="medium"></el-button>
			<el-button size="small"></el-button>
			<el-button size="mini"></el-button>
		</div>
		<div class="group">
			<el-button round></el-button>
			<el-button size="medium" round></el-button>
			<el-button size="small" round></el-button>
			<el-button size="mini" round></el-button>
		</div>
		<div class="group">
			<el-radio v-model="radio" label="1"></el-radio>
			<el-radio v-model="radio" label="2"></el-radio>
		</div>
		<div class="group">
			<el-radio disabled v-model="radio" label=""></el-radio>
			<el-radio disabled v-model="radio" label=""></el-radio>
		</div>
		<div class="group">
			<el-checkbox v-model="checked"></el-checkbox>
		</div>

		<div class="group">
			<el-input v-model="input" placeholder=""></el-input>
			<el-input
				placeholder=""
				v-model="input"
				:disabled="true">
			</el-input>
			<el-input
				placeholder=""
				v-model="input"
				clearable>
			</el-input>
		</div>
		<div class="group">
			<div class="block">
			    <el-date-picker
			      v-model="datetime"
			      type="datetime"
			      placeholder="">
			    </el-date-picker>
			</div>
			<div class="block">
			    <el-date-picker
			      v-model="date"
			      type="date"
			      placeholder="">
			    </el-date-picker>
			</div>
			<el-time-select
			  v-model="time"
			  :picker-options="{
			    start: '08:30',
			    step: '00:15',
			    end: '18:30'
			  }"
			  placeholder="">
			</el-time-select>
		</div>
		<div class="group">
			<el-button :plain="true" @click="open2"></el-button>
			<el-button :plain="true" @click="open3"></el-button>
			<el-button :plain="true" @click="open"></el-button>
			<el-button :plain="true" @click="open4"></el-button>
			<el-button type="text" @click="open5"> Message Box</el-button>
		</div>



	</div>
</template>
<script>
	import {mapGetters,mapActions} from 'vuex';
	export default{
		data () {
	      	return {
	        	radio: '1',
	        	checked:false,
	        	input:'',
	        	datetime:'',
	        	date:'',
	        	time:''
	      	};
	    },
		methods:{
			open() {
		        this.$message('');
		    },
		    open2() {
		        this.$message({
		          message: '',
		          type: 'success'
		        });
		    },

		    open3() {
		        this.$message({
		          message: '',
		          type: 'warning'
		        });
		    },

		    open4() {
		        this.$message.error('');
		    },

		    open5() {
		        this.$confirm(', ?', '', {
		          confirmButtonText: '',
		          cancelButtonText: '',
		          type: 'warning'
		        }).then(() => {
		          this.$message({
		            type: 'success',
		            message: '!'
		          });
		        }).catch(() => {
		          this.$message({
		            type: 'info',
		            message: ''
		          });          
		        });
		    }
		}

	}
</script>


<style rel="stylesheet/less" lang="less" scoped>
  	@import "~@/config/config.less";
	.box{
		padding: 20px;
		height: 100%;
		overflow: auto;
		h1{
			margin-bottom: 20px;
		}
		.group{
			margin: 20px 0;
		}
	}

</style>









